.container
{
    display: flex;
    flex-direction: column;
    background-color: white;
    margin: auto;
    width: 600px;
    margin-top: 200px;
    padding-bottom: 30px;
    height: 650px;
}

.header
{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    width: 100%;
    gap: 10px;
}

.text
{
    font-weight: 700;
    font-size: 35px;
    color: #2f80ed;
    font-family: 'Aclonica';
}

.underline
{
    width: 61px;
    height: 4px;
    background-color: #3183ee;
    border-radius:9px ;
   
}

.inputs
{
    
    display: flex;
    flex-direction: column;
    margin-top: 55px;
    gap: 25px;
   
}

.input
{
    display: flex;
    align-items: center;
    margin: auto;
    height: 80px;
    width: 480px;
    background-color: rgb(220, 224, 224);
    border-radius: 6px;
}

.input img
{
    margin: 0px 30px;
}

.input input
{
    height: 50px;
    width: 400px;
    background-color: transparent;
    border: none;
    outline: none;
    color: rgb(96, 131, 238);
    font-size: 19px;
}

.forgot-password
{
    padding-left: 62px;
    margin-top: 27px;
    color:rgb(168, 171, 171);
    font-size: 18px;
}

.forgot-password span
{
   color: #2f80ed;
   cursor: pointer;
}

.submit-container
{
    display: flex;
    margin: 30px auto;
    gap: 30px;
}

.submit
{
   display: flex;
   justify-content: center;
   align-items: center;
   width: 220px;
   height: 59px;
   color: white;
   background: #2f80ed;
   border-radius: 50px;
   font-size: 20px;
   font-weight: 700px;
   cursor: pointer;
}



.gray
{
  background: #EAEAEA;
  color: #676767;
}


